body{
  background-color: #282c34;
}

h2, h3, h4{
  color: #AB2A3E;
}

.App {
  text-align: center;
  font-family: 'Jua', sans-serif;
}

.disable{
  color: #C0C0C0;
  font-size: 1.3rem;
  padding: 2rem;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cloud-container{
  margin-top: 3rem;
  margin-bottom: 3rem;
  width: 70%;
}



.counter-field{
  font-family: 'Jua', sans-serif;
  font-size: 1.5rem;
  color: rgb(215, 249, 129);
}

footer{
  background-color: #20232a;
  color:white;
  padding-bottom: 2rem;
  padding-top: 3rem;
  border-top: #282c58 1px solid;
}

.moodImg{
  height: 256px;
  width: 256px;
  margin-bottom: 2rem;
}

.page-link{
  background-color: #282c34 !important;
  border-color: #282c34 !important;
  color: white !important;
  cursor: pointer;
}

#char-container{
  padding-bottom: 3rem;
  margin-top: 5rem;
}

.cabecera{
  background-color: #20232a !important;
  color: white !important;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.fs-4{
  color: white !important;
  font-family: 'Jua', sans-serif;
}

.css-yafthl-MuiSlider-markLabel{
  color: white !important;
}